import React, { Component } from "react";
import { observer } from "mobx-react";
import Store from "./Store";
import Styles from "./Styles";
import Form from "./Form";
import GeneForm from "./GeneForm";
import SendQuery from "./SendQuery";
import CellLineForm from "./CellLineForm";
import TextField from "material-ui/TextField";
import FloatingActionButton from "material-ui/FloatingActionButton";
import ContentDelete from "material-ui/svg-icons/navigation/close";
import { Row, Col } from "react-grid-system";
import DropDownMenu from "material-ui/DropDownMenu";
import MenuItem from "material-ui/MenuItem";
import ContentSearch from "material-ui/svg-icons/action/search";
import CircularProgress from "material-ui/CircularProgress";

const regexes = [
  { pattern: /^rs\d+$/, name: "dbSNP" },
  { pattern: /^((HGNC|hgnc):)?\d{1,5}$/, name: "HGNC" },
  { pattern: /^([^ :]+)\:.*?([cgmrp]?)\.?([\*\-0-9]+.[^ ]*)/, name: "HGVS" },
  {
    pattern: /^((AC|AP|NC|NG|NM|NP|NR|NT|NW|XM|XP|XR|YP|ZP)_\d+|(NZ\_[A-Z]{4}\d+))(\.\d+)?/,
    name: "RefSeq"
  },
  { pattern: /^GO:\d{7}$/, name: "GO" }
];

@observer
class EBISC extends Component {
  handleInput = async (e, value) => {
    Store.inputType = "";

    regexes.forEach(x => {
      value.trim().match(x.pattern) ? (Store.inputType = x.name) : false;
    });

    // checking for HGNC
    if (value.length >= 3 && Store.inputType === "") {
      try {
        let result = await Store.fetchStuff(
          "https://rest.ensembl.org/lookup/symbol/homo_sapiens/" +
            value.trim() +
            "?content-type=application/json"
        );
        console.log(result);
        if (result.id) {
          Store.inputType = "Gene Symbol";
        }
      } catch (e) {
        console.log(e);
      }
    }
  };

  handleChange = (event, index, value) => {
    Store.dropdown = value;
  };

  render() {
    return (
      <div style={Styles.spacing}>
        <div>
          <div style={{ width: "100%", position: "relative" }}>
            <TextField
              autoFocus
              fullWidth
              hintStyle={{ color: "red" }}
              name="userInput"
              ref="userInput"
              placeholder={Store.placeholder}
              onChange={this.handleInput}
              underlineStyle={{ display: "none" }}
              style={Styles.input}
            />
            {Store.loading && (
              <div
                style={{
                  top: "14px",
                  right: "-70px",
                  position: "absolute"
                }}>
                <CircularProgress color="#718B98" />
              </div>
            )}
            <div
              style={{
                top: "0px",
                bottom: "0",
                left: "0",
                borderTopLeftRadius: "8px",
                border: "2px solid #546E7A",
                borderBottomLeftRadius: "8px",
                position: "absolute",
                background: "#C4CFD4",
                color: "white"
              }}>
              <DropDownMenu
                disabled={true}
                labelStyle={{
                  lineHeight: "64px",
                  color: "#111518"
                }}
                underlineStyle={{
                  display: "none"
                }}
                value={Store.dropdown}
                onChange={this.handleChange}>
                <MenuItem value={1} primaryText="Search for Cell Lines" />
              </DropDownMenu>
            </div>
            {Store.inputType !== "" &&
              !Store.loading && (
                <div
                  style={{
                    top: "6px",
                    right: "-70px",
                    position: "absolute"
                  }}>
                  <SendQuery />
                </div>
              )}
          </div>
        </div>

        {Store.inputType !== "" && (
          <div>
            <span style={Styles.chipLeft}>{Store.inputType}</span>
          </div>
        )}

        {Store.inputCategory === "Variant" && <Form />}
        {Store.inputCategory === "Genes" && <GeneForm />}
        {Store.inputCategory === "Cell Lines" && <CellLineForm />}

        {/*
        {Store.inputType === "" && (
          <div className="tutorial">
            <Row style={{ display: "none", padding: "16px" }}>
              <Col sm={6}>
                <h2>Search Cell Lines by:</h2>
                <ul>
                  <li>
                    VARIANTS
                    <small>RsIDs - rs12344</small>
                    <small>HGVS - ENST00000003084:c.1431_1433delTTC</small>
                  </li>
                  <li>
                    GENES
                    <small>Symbols - BRCA2</small>
                    <small>GO IDs - GO:1234444</small>
                    <small>RefSeq -NG_007400.1:g.8638G>T</small>
                  </li>
                </ul>
              </Col>
              <Col sm={6}>
                <h2>Search Genes by:</h2>
                <ul>
                  <li>
                    CELL LINES
                    <small>HIPSCI CELL LINE - HPSI0114i-eipl_1</small>
                  </li>
                </ul>
              </Col>
            </Row>
          </div>
        )}


      */}
      </div>
    );
  }
}
export default EBISC;



